.App-link {
  color: #09d3ac;
}

.weather-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.weather-item {
  flex: 1 100%;
  /* flex: 1 0 0; */
  background: #282c34;
  padding: 5px;
  margin: 0.2em;

  line-height: 2em;
  color: white;
  font-weight: bold;
  font-size: 1em;
  text-align: center;

  border-radius: 0.5em;
}

.time {
  display: inline-block;
  margin-right: 0.5em;
  line-height: 1.8em;
  vertical-align: text-bottom;
}

.forecast-date {
  margin-top: 1em;
}

.temperature-number {
  font-size: 2em;
}
.temperature-degree {
  font-size: 1em;
  font-weight: normal;
  position: relative;
  display: inline-block;
  top: -0.7em;
}

@media all and (min-width: 600px) {
  .weather-item {
    flex: 1 0 0;
  }
}
